.howItsWorks {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  width: 100%;
  box-sizing: border-box;
}
.howItsWorksHeader {
}
.howItWorksList {
  width: 100%;
  max-width: 1200px;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;
}
.howItWorksListItem {
  padding-bottom: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: 300;
  font-size: 16px;
}
.howItWorksBulletPoint {
  width: 40px;
  height: 40px;
  border-width: 2px;
  background: #ffa600;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  margin-right: 20px;
}
.howItWorksListItemText {
  display: flex;
  flex-wrap: wrap;
  max-width: 200px;
}

@media screen and (min-width: 800px) {
  .howItWorksList {
    flex-direction: row;
    justify-content: center;
  }
  .howItWorksListItem:not(:last-child) {
    margin-right: 20px;
  }
  .howItWorksListItem {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media screen and (min-width: 1200px) {
  .howItWorksListItem:not(:last-child) {
    margin-right: 60px;
  }
}
